/* ---- CSS --- CENTRO DE ENTRENAMIENTO MATEO --- CSS -----*/


@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-padding-top: 0rem;
}
*, 
*:before,
*:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    margin: 0 auto;

    &.fijar-body {
        overflow: hidden;
    }
    &.body-scroll {
        padding-top: 8.6rem;
    }
}

p {
    font-size: 2rem;
    color: v.$noche;
    line-height: 1.5;  // Interlineado de los p√°rrafos.
}
.contenedor {
    //width: 95%;
    //max-width: 120rem;
    width: min(94%, 120rem);  //Nuevo
    margin: 0 auto;
}

a {
    text-decoration: none;
}

h1, h2, h3 {
    margin: 0 0 5rem 0;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}

img {
    max-width: 100%;
    width: 100%;
    height: auto;
    /* display: block; */
}

.no-margin {
    margin: 0;
    padding: 0;
}
